body {
  font-family: 'Helvetica Neue', Arial, sans-serif;
  background-color: #1c1c1e;
  background-image: linear-gradient(to right, #1c1c1e, #363636);
  color: white;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
}

/* ã‚«ãƒœãƒãƒ£ã®ãƒ•ã‚§ãƒ¼ãƒ‰ã‚¤ãƒ³/æ‹¡å¤§ã‚¢ãƒ‹ãƒ¡ãƒ¼ã‚·ãƒ§ãƒ³ */
.pumpkin-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.8); /* èƒŒæ™¯ã‚’åŠé€æ˜ã« */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  opacity: 0;
  transition: opacity 1.5s ease-in-out; /* ãƒ•ã‚§ãƒ¼ãƒ‰ã®æ™‚é–“ã‚’èª¿æ•´ */
  animation: pumpkinFadeIn 1.5s ease-in-out forwards;
}

.pumpkin-overlay.show {
  opacity: 1;
}

.pumpkin-overlay img {
  width: 50px; /* æœ€åˆã¯å°ã•ã„ã‚µã‚¤ã‚ºã§ã‚¹ã‚¿ãƒ¼ãƒˆ */
  height: auto;
  animation: pumpkinZoom 1.5s ease-in-out forwards; /* æ‹¡å¤§ã‚¢ãƒ‹ãƒ¡ãƒ¼ã‚·ãƒ§ãƒ³ */
}

/* ã‚«ãƒœãƒãƒ£ã®ãƒ•ã‚§ãƒ¼ãƒ‰ã‚¤ãƒ³ã¨æ‹¡å¤§ã‚¢ãƒ‹ãƒ¡ãƒ¼ã‚·ãƒ§ãƒ³ */
@keyframes pumpkinFadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes pumpkinZoom {
  from {
    transform: scale(0.5); /* å°ã•ãè¡¨ç¤ºã•ã‚Œã¦ã„ã‚‹çŠ¶æ…‹ */
  }
  to {
    transform: scale(8); /* å¤§ãããªã‚‹ */
  }
}

/* ã‚³ã‚¦ãƒ¢ãƒªã®ã‚¢ãƒ‹ãƒ¡ãƒ¼ã‚·ãƒ§ãƒ³ */
@keyframes fly {
  0% {
    transform: translateY(0) translateX(-50vw) rotate(0deg);
  }
  100% {
    transform: translateY(-100vh) translateX(50vw) rotate(360deg);
  }
}

.bat {
